@import url(https://fonts.googleapis.com/css2?family=Lora&display=swap);
@font-face {
    font-family: 'HK Grotesk';
    font-style: bold;
    font-weight: 600;
    src: url(/covid-queueing/static/media/HKGrotesk-Bold.97358047.ttf);
}

@font-face {
    font-family: 'HK Grotesk';
    font-style: bold;
    font-weight: 500;
    src: url(/covid-queueing/static/media/HKGrotesk-SemiBold.88b65bd1.ttf);
}


@font-face {
    font-family: 'HK Grotesk';
    font-style: normal;
    font-weight: 400;
    src: url(/covid-queueing/static/media/HKGrotesk-Regular.d0468831.ttf);
}



body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/*** Page ***/
.App {
    background-color: #f9f9f9;
    font-family: 'HK Grotesk', Helvetica, sans-serif;
}

/*** Header ***/
.lead {
    background-color: #f9f9f9;
    padding: 3rem 0 0 0;
}

.lead h1 {
    font-weight: 600;
    text-align: center;
}

.lead h2 {
    font-weight: 500;
    text-align: center;
    font-size: 1.25em;
}

.lead p {
    text-align: center;
}

/* modified from main MGGG site */
.site-header {
    background-color: #f9f9f9;
    margin: 0;
    padding: 1.5rem 1rem 0.5rem 2rem;
    font-size: 1rem;
}

.logo {
    height: 1.5rem;
}

/*** Interactive ***/
.wait-time-chart .alert {
    max-width: 800px;
    margin: 0 auto;
    margin-top: 1rem;
}

.wait-time-params {
    padding-top: 1rem;
    margin: 0 auto;
}

.wait-time-params button {
    font-weight: 600;
}

.wait-time-params label, .wait-time-params select {
    font-weight: 500;
}

.wait-time-params  .form-check label {
    font-weight: 400;
}

.demand-label, .ops-label {
    color: #444;
    font-weight: 600;
}

.simulate-button {
    width: 100%;
    /*background-color: #3a89b8;
    border: 1px solid #3a89b8;*/
}

.invalid-feedback {
    display: block;
}

/*
.simulate-button:hover, .simulate-button:focus, .simulate-button:active {
    background-color: #2b6b98;
}*/

/*** Info text ***/
.info {
    background-color: #f9f9f9;
    padding: 2rem 4rem;
}

.info h2 {
    font-weight: 600;
    font-size: 1.9rem;
    margin: 1rem 4rem;
    padding-bottom: 0.5em;
}

.info p {
    font-size: 1.1em;
    max-width: 950px;
    margin: 1rem 4rem;
    font-family: "Lora", "HK Grotesk", Helvetica, sans-serif;
}

.info:last-of-type {
    padding-bottom: 4rem;
}

@media only screen and (min-width: 1300px) {
    .info h2 {
        text-align: center;
    }
    .info p {
        margin: 1rem auto;
    }
}

@media only screen and (max-width: 576px) {
    .info p, .info h2 {
        margin: 1rem 1.5rem;
    }
}

.info a {
    color: #006b9c;
}


/*** Footer ***/
footer {
    background-color: #2b6b98;
    color: #f9f9f9;
    padding: 3rem;
}

footer p {
    margin-top: 1rem;
}

footer a, footer a:visited {
    color: white;
    font-weight: 600;
    text-decoration: none;
}

footer a:hover, footer a:active {
    text-decoration: underline;
}


